import { EnvironmentFilled, GoogleSquareFilled, PhoneFilled } from "@ant-design/icons";
import { Col, Image, Row } from "antd";
import { isEmpty } from "lodash";
import React from "react";
import Styled from "./Styled";

const StaffProfile = (props) => {

    const { employeeInfo, dataCV } = props
    console.log("employeeInfo", employeeInfo);

    if (isEmpty(employeeInfo)) {
        return <h1>No data</h1>
    } else {
        return (
            <div style={{ padding: 10, overflowY: "scroll", height: 600 }}>
                <Row>
                    <Col span={8} style={{
                        display: "flex",
                        justifyContent: "center", alignItems: "center"
                    }}>
                        <Image
                            style={{ width: 150, height: 150, borderRadius: "50%" }}
                            src={employeeInfo?.employeeInfo?.photoUrl ? employeeInfo?.employeeInfo?.photoUrl : "https://zos.alipayobjects.com/rmsportal/jkjgkEfvpUPVyRjUImniVslZfWPnJuuZ.png"}
                        />
                    </Col>

                    <Col span={16} style={{
                        display: "flex",
                        flexDirection: "column", justifyContent: "center", paddingLeft: 20
                    }}>
                        <h3>{employeeInfo?.employeeInfo?.fullName}</h3>
                        <p style={{ fontSize: "16px", fontStyle: "italic" }}>{employeeInfo?.employeeInfo?.registerContent ? employeeInfo?.employeeInfo?.registerContent : "Thực tập sinh Front-end"}</p>
                        <p>{dataCV.hobby}</p>
                    </Col>
                </Row>

                <Row style={{ backgroundColor: "#289928", color: "#fff", margin: "10px 0" }}>
                    <Col style={{ textAlign: "left", padding: "5px 5px" }} span={8}><GoogleSquareFilled /> {employeeInfo?.employeeInfo?.email}</Col>
                    <Col style={{ textAlign: "center", padding: "5px 5px" }} span={8}><PhoneFilled /> {employeeInfo?.employeeInfo?.phone}</Col>
                    <Col style={{ textAlign: "center", padding: "5px 5px" }} span={8}><EnvironmentFilled /> {employeeInfo?.employeeInfo?.address ? employeeInfo?.employeeInfo?.address : "Hà Nội"}</Col>
                </Row>

                <div>
                    <h4>Mục tiêu</h4>
                    <p>{dataCV.careerGoal}</p>
                </div>

                <div>
                    <h4>Các kỹ năng</h4>
                    <p>{dataCV.skill}</p>
                </div>

                <div>
                    <h4>Kinh nghiệm làm việc</h4>
                    <div>
                        {dataCV.workExperiences.map((item, index) => (
                            <div key={index}>
                                <p>
                                    <span style={{ display: "inline-block", width: "30%" }}> - Công ty: {item.company}</span>
                                    <span style={{ display: "inline-block", width: "30%" }}>Vị trí: {item.position}</span>
                                    <span style={{ display: "inline-block", width: "30%" }}>Mô tả: {item.detail}</span>
                                </p>
                                <p>
                                    <span style={{ display: "inline-block", width: "30%", whiteSpace: "pre-wrap" }}>   Ngày bắt đầu: {item.startDate}</span>
                                    <span style={{ display: "inline-block", width: "30%" }}>Ngày kết thúc: {item.endDate}</span>
                                </p>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        )
    }
}

export default StaffProfile;