import React, { useEffect, useState } from 'react';
import { Button, Col, DatePicker, Form, Input, Row, Table } from 'antd';
import { ActionButton } from 'app/views/general-settings/TableButton';
import { getProposedStaff, postProposedStaff } from '../UpdateHappeningsService';
import moment from 'moment';
import ProposedForm from './ProposedForm';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure({
    autoClose: 1000,
    draggable: false,
    limit: 3,
});
const { TextArea } = Input;

function ProposedCollapse({ formRef, staffInfo, onSend, onDelete }) {
    const [proposedHistory, setProposedHistory] = useState([]);
    const [proposedForm, setProposedForm] = useState(false);
    const [proposedRecord, setProposedRecord] = useState(false);

    const [form] = Form.useForm();

    const handleGetListNewStaff = async (staffInfo) => {
        const res = await getProposedStaff(staffInfo);
        if (res?.data?.code === 200) {
            setProposedHistory(res?.data?.data);
        }
    };

    useEffect(() => {
        handleGetListNewStaff(staffInfo);
    }, [proposedHistory]);

    const addProposeList = async (values) => {
        const staffData = {
            ...proposedHistory,
            ...values,
            employeeId: staffInfo.employeeId,
        };

        const res = await postProposedStaff(staffData);
        if (res?.data?.code === 200) {
            toast.success(res?.data?.message);
            form.resetFields();
            handleShowProposedForm();
        } else {
            toast.warning(res?.data?.message);
        }
    };

    const onReset = () => {
        form.resetFields();
    };

    const handleShowProposedForm = () => {
        setProposedForm(!proposedForm);
    };

    return (
        <div className="content-form-layout">
            <Form onFinish={addProposeList} ref={formRef} form={form}>
                <Row>
                    <Col span={24} md={5}>
                        <Form.Item
                            name="createTime"
                            label="Ngày đăng kí"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <DatePicker placeholder="YYYY-MM-DD" format="YYYY-MM-DD" />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={7}>
                        <Form.Item
                            name="type"
                            label="Loại"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12}>
                        <Form.Item
                            name="note"
                            label="Ghi chú"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12}>
                        <Form.Item
                            name="content"
                            label="Nội dung"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <TextArea
                                autoSize={{
                                    minRows: 1,
                                    maxRows: 3,
                                }}
                            />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12} style={{ display: 'flex', marginTop: '10px', alignItems: 'flex-end' }}>
                        <Form.Item>
                            <Button className="add-form" type="primary" htmlType="submit">
                                Thêm
                            </Button>
                            <Button className="cancel-form" htmlType="button" onClick={onReset} type="primary">
                                Hủy
                            </Button>
                            {/* <Button type="primary" htmlType="button" onClick={handleShowProposedForm}>
                                Lưu
                            </Button> */}
                        </Form.Item>
                    </Col>
                </Row>
            </Form>

            <Table
                columns={[
                    {
                        key: '1',
                        title: 'Hành động',
                        render: (record) => {
                            return (
                                <ActionButton
                                    update
                                    deleted
                                    item={record}
                                    onSelect={(record, method) => {
                                        if (method === 0) {
                                            setProposedRecord(record);
                                            handleShowProposedForm();
                                        } else if (method === 1) {
                                            onDelete();
                                        } else {
                                            alert('Call Selected Here:' + record.id);
                                        }
                                    }}
                                />
                            );
                        },
                    },
                    { key: '2', title: 'Loại', dataIndex: 'type' },
                    {
                        key: '3',
                        title: 'Ngày',
                        render: (record) => {
                            return (
                                <div>
                                    <p>{moment(record.createTime).format('DD-MM-YYYY')}</p>
                                </div>
                            );
                        },
                    },
                    { key: '3', title: 'Nội dung', dataIndex: 'content' },
                    { key: '5', title: 'Ghi chú', dataIndex: 'note' },
                ]}
                rowKey="studentId"
                dataSource={proposedHistory}
                pagination={{
                    defaultPageSize: 10,
                    showSizeChanger: true,
                    pageSizeOptions: ['5', '10', '20', '30'],
                }}
                style={{ margin: '15px 5px 0 5px' }}
            />

            {proposedForm && (
                <ProposedForm
                    open={proposedForm}
                    onOk={handleShowProposedForm}
                    onCancel={handleShowProposedForm}
                    staffInfo={staffInfo}
                    proposedRecord={proposedRecord}
                    onSend={onSend}
                />
            )}
        </div>
    );
}

export default ProposedCollapse;
