import { Breadcrumb, Table } from 'antd';
import React, { useEffect, useState } from 'react';
import ModalUpdateHappenings from './modal-update-happenings';
import './UpdateHappenings.css';
import { getStaff } from './UpdateHappeningsService';
import { HomeFilled } from '@ant-design/icons';
import { ActionButton } from '../general-settings/TableButton';
import moment from 'moment';
import AdditionalRequestModal from './AdditionalRequestModal';
import { ListStatus } from '../general-settings/setting';

const UpdateHappenings = () => {
    const [staffInfo, setStaffInfo] = useState({});
    const [listConcludeData, setListConcludeData] = useState([]);
    const [shouldOpenModalUpdateHappenings, setShouldOpenModalUpdateHappenings] = useState(false);
    const [shouldOpenAdditionalRequestModal, setShouldOpenAdditionalRequestModal] = useState(false);

    const handleGetListNewStaff = async () => {
        const res = await getStaff();
        if (res?.data?.code === 200) {
            setListConcludeData(res?.data?.data);
        }
    };

    useEffect(() => {
        handleGetListNewStaff();
    }, []);

    const openUpdateStatus = () => {
        setShouldOpenModalUpdateHappenings(!shouldOpenModalUpdateHappenings);
    };

    const openAdditionalRequestModal = () => {
        setShouldOpenAdditionalRequestModal(!shouldOpenAdditionalRequestModal);
    };

    return (
        <div className="m-sm-30 updateHappenings">
            <Breadcrumb style={{ padding: '0 0 30px 0' }}>
                <Breadcrumb.Item href="/">
                    <HomeFilled />
                </Breadcrumb.Item>
                <Breadcrumb.Item href="/conclude/manage">
                    <span>Quản lý nhân viên</span>
                </Breadcrumb.Item>
                <Breadcrumb.Item>Quản lý</Breadcrumb.Item>
            </Breadcrumb>

            <Table
                columns={[
                    {
                        key: 'action',
                        title: 'Hành động',
                        width: '10%',
                        render: (record) => {
                            return (
                                <ActionButton
                                    info
                                    view
                                    item={record}
                                    onSelect={(record, method) => {
                                        if (method === 2) {
                                            openAdditionalRequestModal()
                                        } else if (method === 3) {
                                            setStaffInfo(record);
                                            openUpdateStatus();
                                        }
                                    }}
                                />
                            );
                        },
                    },
                    { key: 'code', title: 'Mã nhân viên', dataIndex: 'code', width: '12%' },
                    { key: 'fullName', title: 'Họ và tên', dataIndex: 'fullName', width: '15%' },
                    {
                        key: 'dateOfBirth', title: 'Ngày sinh', width: '10%',
                        render: (record) => {
                            return (
                                <div>
                                    <p>{record?.dateOfBirth ? moment(record?.dateOfBirth).format("DD-MM-YYYY") : ""}</p>
                                </div>
                            );
                        },
                    },
                    { key: 'gender', title: 'Giới tính', dataIndex: 'gender', width: '10%' },
                    { key: 'address', title: 'Địa chỉ', dataIndex: 'address', width: '25%' },
                    {
                        key: 'status',
                        title: 'Trạng thái',
                        render: (record) => <ListStatus record={record} />
                    },
                ]}
                dataSource={listConcludeData}
                pagination={{
                    defaultPageSize: 10,
                    showSizeChanger: true,
                    pageSizeOptions: ['5', '10', '20', '30'],
                }}
            />

            {shouldOpenAdditionalRequestModal && (
                <AdditionalRequestModal
                    open={shouldOpenAdditionalRequestModal}
                    openAdditionalRequestModal={openAdditionalRequestModal}
                />
            )}

            {shouldOpenModalUpdateHappenings && (
                <ModalUpdateHappenings
                    open={shouldOpenModalUpdateHappenings}
                    staffInfo={staffInfo}
                    listConcludeData={listConcludeData}
                    openUpdateStatus={openUpdateStatus}
                />
            )}
        </div>
    );
};

export default UpdateHappenings;
