import { Button, Col, DatePicker, Form, Input, Row, Table } from 'antd';
import { ActionButton } from 'app/views/general-settings/TableButton';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { getSalaryStaff, postSalaryStaff } from '../UpdateHappeningsService';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import SalaryIncreaseModal from './SalaryIncreaseModal';

const { TextArea } = Input;

toast.configure({
    autoClose: 1000,
    draggable: false,
    limit: 3,
});

function SalaryIncreaseCollapse({ formRef, staffInfo, onSend, onDelete }) {
    const [salaryHistory, setSalaryHistory] = useState([]);
    const [salaryForm, setSalaryForm] = useState(false);
    const [salaryRecord, setSalaryRecord] = useState({});

    const [form] = Form.useForm();

    const handleGetListNewStaff = async (staffInfo) => {
        const res = await getSalaryStaff(staffInfo);
        if (res?.data?.code === 200) {
            setSalaryHistory(res?.data?.data);
        }
    };

    useEffect(() => {
        handleGetListNewStaff(staffInfo);
    }, [salaryHistory]);

    const handleShowSalaryForm = () => {
        setSalaryForm(!salaryForm);
    };

    const addSalaryIncreaseList = async (values) => {
        const staffData = {
            ...salaryHistory,
            ...values,
            employeeId: staffInfo.employeeId,
        };
        const res = await postSalaryStaff(staffData);
        if (res?.data?.code === 200) {
            toast.success(res?.data?.message);
            form.resetFields();
            handleShowSalaryForm();
        } else {
            toast.warning(res?.data?.message);
        }
    };

    const onReset = () => {
        form.resetFields();
    };

    return (
        <div className="content-form-layout">
            <Form onFinish={addSalaryIncreaseList} ref={formRef} form={form}>
                <Row>
                    <Col span={24} md={12} xl={3}>
                        <Form.Item
                            name="count"
                            label="Lần thứ"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                {
                                    pattern: '^([-]?[0-9][0-9]*|0)$',
                                    message: 'Trường này phải là số',
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12} xl={4}>
                        <Form.Item
                            name="salaryScale"
                            label="Bậc"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12} xl={5}>
                        <Form.Item
                            name="createTime"
                            label="Ngày tăng lương"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <DatePicker placeholder="YYYY-MM-DD" format="YYYY-MM-DD" />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12} xl={12}>
                        <Form.Item
                            name="note"
                            label="Ghi chú"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={12} xl={12}>
                        <Form.Item
                            name="reason"
                            label="Lý do tăng lương"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <TextArea
                                autoSize={{
                                    minRows: 1,
                                    maxRows: 3,
                                }}
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        span={24}
                        md={12}
                        xl={12}
                        style={{ display: 'flex', marginTop: '10px', alignItems: 'flex-end' }}
                    >
                        <Form.Item>
                            <Button className="add-form" type="primary" htmlType="submit">
                                Thêm
                            </Button>
                            <Button className="cancel-form" htmlType="button" onClick={onReset} type="primary">
                                Hủy
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>

            <Table
                columns={[
                    {
                        key: '1',
                        title: 'Hành động',
                        render: (record) => {
                            return (
                                <ActionButton
                                    update
                                    deleted
                                    item={record}
                                    onSelect={(record, method) => {
                                        if (method === 0) {
                                            setSalaryRecord(record);
                                            handleShowSalaryForm();
                                        } else if (method === 1) {
                                            onDelete();
                                        } else {
                                            alert('Call Selected Here:' + record.id);
                                        }
                                    }}
                                />
                            );
                        },
                    },
                    { key: '2', title: 'Lần', dataIndex: 'count' },
                    { key: '3', title: 'Bậc', dataIndex: 'salaryScale' },
                    {
                        key: '4',
                        title: 'Ngày tăng lương',
                        render: (record) => {
                            return (
                                <div>
                                    <p>{moment(record.createTime).format('DD-MM-YYYY')}</p>
                                </div>
                            );
                        },
                    },
                    { key: '5', title: 'Lý do', dataIndex: 'reason' },
                    { key: '6', title: 'Ghi chú', dataIndex: 'note' },
                ]}
                rowKey="studentId"
                dataSource={salaryHistory}
                pagination={{
                    defaultPageSize: 10,
                    showSizeChanger: true,
                    pageSizeOptions: ['5', '10', '20', '30'],
                }}
                style={{ margin: '15px 5px 0 5px' }}
            />

            {salaryForm && (
                <SalaryIncreaseModal
                    open={salaryForm}
                    onOk={handleShowSalaryForm}
                    onCancel={handleShowSalaryForm}
                    staffInfo={staffInfo}
                    salaryRecord={salaryRecord}
                    onSend={onSend}
                />
            )}
        </div>
    );
}

export default SalaryIncreaseCollapse;
