import { Button, Col, DatePicker, Form, Input, Row, Table } from 'antd';
import React, { useEffect, useState } from 'react';
import { getPromotedStaff, postPromotedStaff } from '../UpdateHappeningsService';
import { ActionButton } from 'app/views/general-settings/TableButton';
import moment from 'moment';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import PromotedModal from './PromotedModal';

toast.configure({
    autoClose: 300,
    draggable: false,
    limit: 3,
});

const { TextArea } = Input;

function PromotedCollapse({ formRef, staffInfo, onSend, onDelete }) {
    const [promotedHistory, setPromotedHistory] = useState([]);
    const [promotedForm, setPromotedForm] = useState(false);
    const [promotedRecord, setPromotedRecord] = useState(false);

    const [form] = Form.useForm();

    const handleGetListNewStaff = async (staffInfo) => {
        const res = await getPromotedStaff(staffInfo);
        if (res?.data?.code === 200) {
            setPromotedHistory(res?.data?.data);
        }
    };

    useEffect(() => {
        handleGetListNewStaff(staffInfo);
    }, [promotedHistory]);

    const addPromotedList = async (values) => {
        const staffData = {
            ...promotedHistory,
            ...values,
            employeeId: staffInfo.employeeId,
        };
        const res = await postPromotedStaff(staffData);
        if (res?.data?.code === 200) {
            toast.success(res?.data?.message);
            form.resetFields();
            handleShowPromotedForm();
        } else {
            toast.warning(res?.data?.message);
        }
    };

    const onReset = () => {
        form.resetFields();
    };

    const handleShowPromotedForm = () => {
        setPromotedForm(!promotedForm);
    };

    return (
        <div className="content-form-layout">
            <Form onFinish={addPromotedList} ref={formRef} form={form}>
                <Row>
                    <Col span={24} md={8} xl={5}>
                        <Form.Item
                            name="createTime"
                            label="Ngày thăng chức"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                            ]}
                        >
                            <DatePicker placeholder="YYYY-MM-DD" format="YYYY-MM-DD" />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={8} xl={3}>
                        <Form.Item
                            name="lanThu"
                            label="Lần thứ"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                                {
                                    pattern: '^([-]?[0-9][0-9]*|0)$',
                                    message: 'Trường này phải là số',
                                },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={8} xl={8}>
                        <Form.Item
                            name="oldPosition"
                            label="Chức vụ cũ"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={8} xl={8}>
                        <Form.Item
                            name="newPosition"
                            label="Chức vụ hiện tại"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <Input />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={8} xl={8}>
                        <Form.Item
                            name="reason"
                            label="Lý do"
                            rules={[
                                {
                                    required: true,
                                    message: 'Bắt buộc phải chọn trường này!',
                                },
                                { whitespace: true, message: 'không được để khoảng trằng trống' },
                            ]}
                        >
                            <TextArea
                                autoSize={{
                                    minRows: 1,
                                    maxRows: 3,
                                }}
                            />
                        </Form.Item>
                    </Col>

                    <Col span={24} md={8} xl={8}>
                        <Form.Item
                            name="note"
                            label="Ghi chú"
                            rules={[
                                {
                                    required: true,
                                },
                            ]}
                        >
                            <TextArea
                                autoSize={{
                                    minRows: 1,
                                    maxRows: 3,
                                }}
                            />
                        </Form.Item>
                    </Col>

                    <Col
                        span={24}
                        md={24}
                        xl={8}
                        style={{ display: 'flex', marginTop: '10px', alignItems: 'flex-end' }}
                    >
                        <Form.Item>
                            <Button className="add-form" type="primary" htmlType="submit">
                                Thêm
                            </Button>
                            <Button className="cancel-form" htmlType="button" onClick={onReset} type="primary">
                                Hủy
                            </Button>
                        </Form.Item>
                    </Col>
                </Row>
            </Form>

            <Table
                columns={[
                    {
                        key: '1',
                        title: 'Hành động',
                        render: (record) => {
                            return (
                                <ActionButton
                                    update
                                    deleted
                                    item={record}
                                    onSelect={(record, method) => {
                                        if (method === 0) {
                                            setPromotedRecord(record);
                                            handleShowPromotedForm();
                                        } else if (method === 1) {
                                            onDelete();
                                        } else {
                                            alert('Call Selected Here:' + record.id);
                                        }
                                    }}
                                />
                            );
                        },
                    },
                    { key: '2', title: 'Chức vụ cũ', dataIndex: 'oldPosition' },
                    { key: '3', title: 'Chức vụ hiện tại', dataIndex: 'newPosition' },
                    {
                        key: '4',
                        title: 'Ngày',
                        render: (record) => {
                            return (
                                <div>
                                    <p>{moment(record.createTime).format('DD-MM-YYYY')}</p>
                                </div>
                            );
                        },
                    },
                    { key: '5', title: 'Lý do', dataIndex: 'reason' },
                    { key: '6', title: 'Ghi chú', dataIndex: 'note' },
                ]}
                rowKey="studentId"
                dataSource={promotedHistory}
                pagination={{
                    defaultPageSize: 10,
                    showSizeChanger: true,
                    pageSizeOptions: ['5', '10', '20', '30'],
                }}
                style={{ margin: '15px 5px 0 5px' }}
            />

            {promotedForm && (
                <PromotedModal
                    open={promotedForm}
                    onOk={handleShowPromotedForm}
                    onCancel={handleShowPromotedForm}
                    staffInfo={staffInfo}
                    promotedRecord={promotedRecord}
                    onSend={onSend}
                />
            )}
        </div>
    );
}

export default PromotedCollapse;
