import { Form, Input } from 'antd';
import moment from 'moment';
import React from 'react';

function SalaryIncreaseForm({ onChangeValue, staffInfo, salaryRecord }) {
    return (
        <Form>
            <div className="top-form-information">
                <div className="top-left-form-information">
                    <div>
                        <Form.Item onChange={onChangeValue} name="organ" label="CƠ QUAN:  ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item onChange={onChangeValue} name="company" label="Công ty:  ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item onChange={onChangeValue} name="number" label="Số:  ">
                            <Input />
                        </Form.Item>
                    </div>
                </div>
                <div className="top-right-form-information">
                    <h4 style={{ textTransform: 'uppercase', fontSize: '20px' }}>Cộng HÒA XÃ HỘI CHỦ NGHĨA VIỆT NAM</h4>
                    <p>
                        <span style={{ fontSize: '18px', borderBottom: '2px solid #4d4d4d', paddingBottom: '15px' }}>
                            Độc lập - Tự do - Hạnh Phúc
                        </span>
                    </p>
                </div>
            </div>
            <div className="modal_content">
                <h3
                    style={{
                        textTransform: 'uppercase',
                        textAlign: 'center',
                        padding: '30px 0 0',
                        fontSize: '18px',
                    }}
                >
                    QUYẾT ĐỊNH
                </h3>
                <p style={{ textAlign: 'center' }}>Về việc điều chỉnh tăng lương cho Nhân Viên</p>
                <div>
                    <Form.Item
                        onChange={onChangeValue}
                        name="regulations"
                        label="- Căn cứ tại quy chế, Điều lệ Công ty: "
                    >
                        <Input />
                    </Form.Item>
                </div>
                <p>- Căn cứ vào hợp đồng lao động với người lao động</p>
                <p>Xét những đóng góp của người lao động và đề nghị của trưởng phòng nhân sự</p>
                <h3
                    style={{
                        textTransform: 'uppercase',
                        textAlign: 'center',
                        padding: '30px 0 0',
                        fontSize: '18px',
                    }}
                >
                    GIÁM ĐỐC QUYẾT ĐỊNH
                </h3>
                <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                    <Form.Item onChange={onChangeValue} name="dateSalary" label="Điều 1: kể từ ngày ">
                        <Input defaultValue={moment(salaryRecord.createTime).format('DD-MM-YYYY')} />
                    </Form.Item>
                    <Form.Item onChange={onChangeValue} name="fullName" label="Điều chỉnh mức lương của Ông/Bà ">
                        <Input defaultValue={staffInfo.fullName} />
                    </Form.Item>
                    <Form.Item onChange={onChangeValue} name="rank" label="sẽ là bậc ">
                        <Input defaultValue={salaryRecord.salaryScale} style={{ width: '50px' }} />
                    </Form.Item>
                </div>
                <p>Điều 2: Các Ông/Bà Phòng Nhân Sự, Phòng Tài Chính Kế Toán căn cứ quyết định thi hành</p>
            </div>
            <div className="bottom-form-information">
                <div className="bottom-left-form-information">
                    <p>Nơi nhận đơn</p>
                    <p>TỔNG GIÁM ĐỐC</p>
                    <div>
                        <Form.Item onChange={onChangeValue} name="managerName">
                            <Input style={{ width: '30%' }} />
                        </Form.Item>
                        <Form.Item onChange={onChangeValue} name="managerFullName">
                            <Input />
                        </Form.Item>
                    </div>
                </div>
                <div className="bottom-right-form-information">
                    <div
                        className="date-signature"
                        style={{ display: 'flex', alignItems: 'center', marginBottom: '15px' }}
                    >
                        Hà nội,
                        <Form.Item name="day" onChange={onChangeValue}>
                            <Input className="day" />
                        </Form.Item>
                        ,
                        <Form.Item name="date" onChange={onChangeValue}>
                            <Input />
                        </Form.Item>
                        tháng
                        <Form.Item name="month" onChange={onChangeValue}>
                            <Input />
                        </Form.Item>
                        ,
                        <Form.Item name="year" onChange={onChangeValue}>
                            <Input className="year" />
                        </Form.Item>
                    </div>
                    <p>Người làm đơn</p>
                    <p>{staffInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                    <p>{staffInfo.fullName}</p>
                </div>
            </div>
        </Form>
    );
}

export default SalaryIncreaseForm;
