import { Modal } from 'antd';
import React from 'react';

function AdditionalRequestModal({ open, openAdditionalRequestModal }) {
    const onOk = () => {
        console.log('ok');
    };
    return (
        <Modal
            centered
            okText="Chỉnh sửa"
            title="Yêu cầu bổ sung"
            cancelText="HỦY"
            open={open}
            onOk={onOk}
            onCancel={openAdditionalRequestModal}
            width={'50vw'}
        >
            <div>
                <p style={{ margin: 0, padding: '20px 0' }}>
                    Yêu cầu của lãnh đão về chỉnh sửa form Yêu cầu của lãnh đão về chỉnh sửa form Yêu cầu của lãnh đão
                    về chỉnh sửa form Yêu cầu của lãnh đão về chỉnh sửa form Yêu cầu của lãnh đão về chỉnh sửa form
                </p>
            </div>
        </Modal>
    );
}

export default AdditionalRequestModal;
