import { Button, Form, Input, Modal } from 'antd';
import React from 'react';

function RegisterProfileForm({ open, onCancel, staffInfo, onSend }) {
    return (
        <Modal
            className="information_modal"
            centered
            title="Biểu mẫu"
            open={open}
            onCancel={onCancel}
            width={'70vw'}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hủy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trình lãnh đạo
                </Button>,
                <Button className="btn-save" key="3">
                    Lưu
                </Button>,
            ]}
        >
            <Form>
                <div
                    style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        textAlign: 'center',
                    }}
                >
                    <h4 style={{ fontSize: 18 }}>CỘNG HÒA XÃ HỘI CHỦ NGHĨA VIỆT NAM</h4>
                    <h5>
                        <span style={{ borderBottom: '2px solid #666', paddingBottom: '3px' }}>
                            Độc lập - Tự do - Hạnh Phúc
                        </span>
                    </h5>
                    <h3 style={{ fontSize: 20, marginTop: '30px' }}>ĐĂNG KÝ HỒ SƠ</h3>
                </div>

                <div style={{ marginTop: '50px' }}>
                    <p>Kính gửi ban giám đốc công ty OceanTech</p>
                    <div>
                        <Form.Item name="fullName" label="1. Tôi tên là: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="profileName" label="2. Tên hồ sơ: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="dateRegister" label="3. Ngày đăng ký: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="note" label="4. Ghi chú: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <div>
                        <Form.Item name="content" label="5. Nội dung: ">
                            <Input />
                        </Form.Item>
                    </div>
                    <p>Kính mong Công ty / cá nhân có thẩm quyền xem xét hồ sơ và xét duyệt hồ sơ cho tôi.</p>
                    <p>
                        Tôi xin cam đoan những thông tin trên hoàn toàn đúng sự thật, nếu có gì sai sót tôi xin chịu
                        trách nhiệm trước Công ty và trước pháp luật.
                    </p>
                    <p>Tôi xin chân thành cảm ơn!</p>
                </div>

                <div>
                    <div className="modal_signature">
                        <div
                            className="date-signature"
                            style={{ display: 'flex', alignItems: 'center', marginBottom: '15px' }}
                        >
                            Hà nội,
                            <Form.Item name="day">
                                <Input className="day" />
                            </Form.Item>
                            ,
                            <Form.Item name="date">
                                <Input />
                            </Form.Item>
                            tháng
                            <Form.Item name="month">
                                <Input />
                            </Form.Item>
                            ,
                            <Form.Item name="year">
                                <Input className="year" />
                            </Form.Item>
                        </div>
                        <p>Người khai</p>
                        <p>{staffInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                        <p>{staffInfo.fullName}</p>
                    </div>
                </div>
            </Form>
        </Modal>
    );
}

export default RegisterProfileForm;
