import axios from 'axios';
import ConstantList from '../../appConfig';
const API_PATH_employees = ConstantList.API_ENPOINT;

export const getListEndStaff = () => {
    const url = `${API_PATH_employees}/employees?statuses=8,9,11,12,13&page=1&size=20`;

    return axios.get(url, {});
};

export const putListEndStaff = (data) => {
    const url = `${API_PATH_employees}/employees/${data.employeeId}`;

    return axios.put(url, data);
};
