import React, { useEffect } from "react";
import { Modal } from 'antd';
import { Tabs } from 'antd';
import StaffProfile from "./staff-profile";
import DiplomaInfo from "./diploma-info";
import CurriculumVitae from "./curriculum-vitae";
import { getStaffById } from "../CreateStaffService";
import { useState } from "react";
import styled from "styled-components";

const TabPane = Tabs.TabPane

const DialogStaffInfo = ({ open, handleClose, employeeId }) => {
    const [employeeInfo, setEmployeeInfo] = useState({})

    const onChange = (key) => { };

    const dataCV = {
        careerGoal: "Trở thành một game thủ chuyên nghiệp thi đấu cho GAM ESPORTS",
        skill: "HTML, CSSS, JS",
        hobby: "Nhảy dây, đá bóng, đánh bài",
        workExperiences: [
            {
                company: "Mitsu yanako",
                position: "Intern",
                detail: "Thực hiện 2 dự án",
                startDate: "2022-09-06",
                endDate: "2022-09-07"
            },
            {
                company: "Monika yasuo",
                position: "Fresher",
                detail: "Thực hiện 5 dự án",
                startDate: "2021-03-03",
                endDate: "2022-05-08"
            },
            {
                company: "Otika",
                position: "Leader",
                detail: "Thực hiện 12 dự án",
                startDate: "2020-09-04",
                endDate: "2022-06-02"
            }
        ]
    }

    const dataResume = {
        fullName: "Nguyễn Văn A",
        commonName: "A Tuấn",
        currentAddress: "Hà Nội",
        citizenId: "039235819586",
        citizenIdIssuanceDate: "2022-09-09",
        citizenIdIssuingAuthority: "Công an Hà Nội",
        ethnicity: "Kinh",
        religion: "Không"
    }

    const handleGetStaffById = async (employeeId) => {
        const res = await getStaffById({ employeeId })

        console.log(" res", res);

        if (res?.data?.code === 200) {
            setEmployeeInfo(res?.data?.data)
        }

    }

    useEffect(() => {
        if (employeeId) {
            handleGetStaffById(employeeId)
        }
    }, [])

    return (
        <StyledModal
            title="Hồ sơ nhân viên"
            centered
            open={open}
            width={1000}
            onCancel={handleClose}
            footer={null}
        >
            <Tabs defaultActiveKey="1" tabPosition="left" onChange={onChange}>
                <TabPane tab="HỒ SƠ" key="1">
                    <StaffProfile
                        employeeInfo={employeeInfo}
                        dataCV={dataCV}
                    />
                </TabPane>
                <TabPane tab="SƠ YẾU LÝ LỊCH" key="2">
                    <CurriculumVitae
                        employeeInfo={employeeInfo}
                        dataResume={dataResume}
                    />
                </TabPane>
                <TabPane tab="DANH SÁCH VĂN BẰNG" key="3">
                    <DiplomaInfo
                        employeeInfo={employeeInfo}
                        dataCV={dataCV}
                    />
                </TabPane>
            </Tabs>
        </StyledModal>
    );
}


export default DialogStaffInfo;

const StyledModal = styled(Modal)`
    .ant-modal {
        height: 90vh;
    }
    .ant-modal-body {
        padding: 20px 20px 0;
        height: 82vh;
        overflow-y: hidden;
    }
    .ant-modal-content {
        height: 90vh;
        overflow: hidden;
    }
    
`