import {
    DeleteFilled,
    EditFilled,
    EyeFilled,
    HomeFilled,
} from '@ant-design/icons';
import { Breadcrumb, Button, Form, Table, Tooltip } from 'antd';
import React, { useEffect, useState } from 'react';
import Styled from './CreateStaffStyled';
import DialogStaff from './dialog-staff';
import DialogStaffInfo from './dialog-staff-info';
// import data from '../../db.json';
import DialogDeleteStaff from './dialog-delete-staff';
import { getStaff, getTotalItem } from "./CreateStaffService";
import SearchStaff from './Search';
import { ListStatus } from '../general-settings/setting';

const CreateStaff = () => {
    const [listNewStaff, setListNewStaff] = useState([]);
    const [dataInitial, setDataInitial] = useState([])
    const [searchInfo, setSearchInfo] = useState("")
    const [shouldOpenCreateStaffDialog, setShouldOpenCreateStaffDialog] = useState(false);
    const [shouldOpenStaffInfoDialog, setShouldOpenStaffInfoDialog] = useState(false);
    const [shouldOpenDeleteStaffDialog, setShouldOpenDeleteStaffDialog] = useState(false);
    const [employeeId, setEmployeeId] = useState(0);
    const [page, setPage] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [total, setTotal] = useState(0);

    const [form] = Form.useForm();

    const handleGetListNewStaff = async (current, pageSize) => {

        const res = await getStaff(current, pageSize)
        if (res?.data?.code === 200) {
            setListNewStaff(res?.data?.data)
            setDataInitial(res?.data?.data)
        }
    }

    useEffect(() => {
        handleGetListNewStaff(page, pageSize);
    }, [page])

    const openCreateStaffDialog = () => {
        setShouldOpenCreateStaffDialog(true);
    };

    const onClickDetail = (record) => {
        setEmployeeId(record.employeeId);
        setShouldOpenStaffInfoDialog(true);
    };

    const handleClose = () => {
        setShouldOpenCreateStaffDialog(false);
        setShouldOpenStaffInfoDialog(false);
        setShouldOpenDeleteStaffDialog(false);
        form.resetFields();
        setEmployeeId(0)
    }

    const onClickEdit = (record) => {
        setEmployeeId(record.employeeId);
        setShouldOpenCreateStaffDialog(true);
    };

    const onClickDelete = (record) => {
        setEmployeeId(record.employeeId);
        setShouldOpenDeleteStaffDialog(true);
    }

    const handleSearch = () => {

        const dataSearch = dataInitial.filter(item => (
            item.code.includes(searchInfo) ||
            item.fullName.includes(searchInfo) ||
            item.email.includes(searchInfo) ||
            item.phone.includes(searchInfo) ||
            item.citizenId.includes(searchInfo)
        ))

        setListNewStaff(dataSearch);
    }

    useEffect(() => {
        handleSearch();
    }, [searchInfo])

    const handleGetTotalItem = async () => {
        const res = await getTotalItem()
        setTotal(res?.data?.data)
    }

    useEffect(() => {
        handleGetTotalItem()
    }, [])



    const handlePaginationChange = (page, pageSize) => {
        setPage(page);
        setPageSize(pageSize);
    };

    return (
        <Styled>
            <div className="m-sm-30">
                <Breadcrumb style={{ padding: '0 0 30px 0' }}>
                    <Breadcrumb.Item href="/">
                        <HomeFilled />
                    </Breadcrumb.Item>
                    <Breadcrumb.Item href="/conclude/manage">
                        <span>Quản lý nhân viên</span>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item>Tạo mới</Breadcrumb.Item>
                </Breadcrumb>

                <div className='set-space-between'>
                    <Button size="middle" onClick={openCreateStaffDialog} className="btn-add-new">TẠO MỚI</Button>
                    <SearchStaff searchInfo={searchInfo} setSearchInfo={setSearchInfo} />
                </div>
                {shouldOpenCreateStaffDialog && (
                    <DialogStaff
                        open={shouldOpenCreateStaffDialog}
                        handleClose={handleClose}
                        employeeId={employeeId}
                        setEmployeeId={setEmployeeId}
                    />
                )}
                {shouldOpenStaffInfoDialog && (
                    <DialogStaffInfo open={shouldOpenStaffInfoDialog} handleClose={handleClose} employeeId={employeeId} />
                )}

                {shouldOpenDeleteStaffDialog && (
                    <DialogDeleteStaff
                        open={shouldOpenDeleteStaffDialog}
                        handleClose={handleClose}
                        employeeId={employeeId}
                    />
                )}

                <Table
                    dataSource={listNewStaff}
                    columns={[
                        {
                            key: 'action',
                            title: 'Thao tác',
                            dataIndex: 'action',
                            align: 'center',
                            width: '10%',
                            render: (text, record) => {
                                return (
                                    <div style={{ display: "flex", gap: '10px', justifyContent: 'center' }} >

                                        {(record.status === 1 || record.status === 4 || record.status === 6) && (<Tooltip placement="bottom" title="chỉnh sửa">
                                            <EditFilled
                                                onClick={() => onClickEdit(record)}
                                                style={{ color: "#0d4ca5", fontSize: '16px' }}
                                            />
                                        </Tooltip>)}

                                        {(record.status === 2 || record.status === 3) && (<Tooltip placement="bottom" title="thông tin nhân viên">
                                            <EyeFilled
                                                style={{ color: "rgb(40, 153, 40)", fontSize: '16px' }}
                                                onClick={() => onClickDetail(record)}
                                            />
                                        </Tooltip>)}

                                        <Tooltip placement="bottom" title="xóa">
                                            <DeleteFilled
                                                style={{ color: "red", fontSize: '16px' }}
                                                onClick={() => onClickDelete(record)}
                                            />
                                        </Tooltip>
                                    </div>
                                );
                            },
                        },
                        { key: 'code', title: 'Mã nhân viên', dataIndex: 'code', width: '12%' },
                        { key: 'fullName', title: 'Tên nhân viên', dataIndex: 'fullName', width: '16%' },
                        { key: 'email', title: 'Email', dataIndex: 'email', width: '16%' },
                        { key: 'phone', title: 'Số điện thoại', dataIndex: 'phone', width: '16%' },
                        { key: 'citizenId', title: 'Mã CCCD/CMT', dataIndex: 'citizenId', width: '16%' },
                        {
                            key: 'status',
                            title: 'Trạng thái',
                            width: '14%',
                            render: (record) => <ListStatus record={record} />
                        },
                    ]}
                    pagination={{
                        current: page,
                        pageSize: pageSize,
                        total: total,
                        onChange: handlePaginationChange,
                        showSizeChanger: true,
                        defaultPageSize: 10,
                        pageSizeOptions: ['5', '10', '20'],
                    }}
                />
            </div>
        </Styled>
    );
};

export default CreateStaff;
