import React, { useState } from "react";
import { Button, Form, Modal } from 'antd';
import { Tabs } from 'antd';
import CurriculumVitae from "./curriculum-vitae";
import styled from "styled-components";
import DialogSendLeader from "./dialog-send-leader";
import DiplomaInfo from "./diploma-info";
import StaffProfile from "./staff-profile";
import { getFormStaff, updateFormStaff } from "../../CreateStaffService";
import { useEffect } from "react";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

toast.configure({
    autoClose: 1000,
    draggable: false,
    limit: 3
});

const TabPane = Tabs.TabPane

const DialogRegister = ({ open, employeeId, staffInfo, handleClose, handleCloseDialogRegister }) => {
    console.log(staffInfo)
    const [profile, setProfile] = useState([])
    const [shouldOpenPopupSendLeader, setShouldOpenPopupSendLeader] = useState(false)
    // const [dataCv, setDataCv] = useState({})

    const [form] = Form.useForm();

    const handleGetApi = async (employeeId) => {
        const res = await getFormStaff({ employeeId })
        console.log(res)
        if (res?.cv?.code === 200) {
            setProfile(res?.data?.data)
        }
        // setDataCv(res?.data?.data?.cv)
        const cvEmployee = res?.data?.data?.cv
        // const workExperiences = res?.data?.data?.cv?.workExperiences
        const dataResume = res?.data?.data?.resume

        form.setFieldsValue({
            careerGoal: cvEmployee?.careerGoal,
            hobby: cvEmployee?.hobby,
            skill: cvEmployee?.skill,
            currentAddress: dataResume?.currentAddress,
            citizenIdIssuanceDate: dataResume?.citizenIdIssuanceDate,
            citizenIdIssuingAuthority: dataResume?.citizenIdIssuingAuthority,
            ethnicity: dataResume?.ethnicity,
            religion: dataResume?.religion,
        });
    }

    useEffect(() => {
        handleGetApi(employeeId)
    }, [])

    console.log(profile)


    const onChange = (key) => { };

    const openPopupSendLeader = () => {
        setShouldOpenPopupSendLeader(true)
    }

    const handleClosePopupSendLeader = () => {
        setShouldOpenPopupSendLeader(false)
    }

    const handleSubmitStaffRegister = async (values) => {
        const staffData = {
            employeeId,
            resume: { ...values },
            cv: {
                careerGoal: values.careerGoal,
                hobby: values.hobby,
                skill: values.skill,
                workExperiences: [{
                    "company": "asdvas",
                    "position": "asd",
                    "detail": "asd",
                    "startDate": "2022-09-09",
                    "endDate": "2022-09-09"
                },]
            }
        }

        console.log(staffData)

        const res = await updateFormStaff(staffData)
        console.log(res)

        if (res?.data?.code === 200) {
            toast.success('Thêm thành công')
            setProfile(res?.data?.data)
        } else {
            toast.warning(res?.data?.message)
        }
    };

    console.log(profile)

    return (
        <StyledModal
            title="Thông tin hồ sơ"
            centered
            open={open}
            width={1000}
            onCancel={handleCloseDialogRegister}
            footer={null}
        >
            <Form
                form={form}
                onFinish={handleSubmitStaffRegister}
                layout="vertical"
            >
                <Tabs defaultActiveKey="1" tabPosition="left" onChange={onChange}>
                    <TabPane tab="HỒ SƠ" key="1">
                        <StaffProfile
                            staffInfo={staffInfo}
                            profile={profile}
                        />
                    </TabPane>
                    <TabPane tab="SƠ YẾU LÝ LỊCH" key="2">
                        <CurriculumVitae
                            staffInfo={staffInfo}
                        />
                    </TabPane>
                    <TabPane tab="DANH SÁCH VĂN BẰNG" key="3">
                        <DiplomaInfo
                            staffInfo={staffInfo.certificates}
                        />
                    </TabPane>
                </Tabs>
                <div className="btn-dialog-register">
                    <Button className="btn-save" htmlType="submit">
                        LƯU
                    </Button>
                    <Button className="btn-register" onClick={() => openPopupSendLeader()} >
                        TRÌNH LÃNH ĐẠO
                    </Button>
                    <Button className="btn-close" onClick={handleCloseDialogRegister}>
                        HỦY
                    </Button>
                </div>

                {shouldOpenPopupSendLeader && (
                    <DialogSendLeader
                        open={shouldOpenPopupSendLeader}
                        employeeId={employeeId}
                        handleClosePopupSendLeader={handleClosePopupSendLeader}
                        handleClose={handleClose}
                        handleCloseDialogRegister={handleCloseDialogRegister}
                    />
                )}
            </Form>
        </StyledModal>
    );
}


export default DialogRegister;

const StyledModal = styled(Modal)`
.ant-tabs-nav .ant-tabs-tab+.ant-tabs-tab{
    margin: 10px 0 0 0;
}
    .ant-modal {
        height: 90vh;
    }
    .ant-modal-body {
        padding: 20px 0 90px 0;
        height: 81vh;
        overflow-y: unset;
    }
    .ant-tabs-tabpane{
        padding-left: 0!important;
        overflow: unset;
    }
    .ant-modal-content {
        height: 90vh;
        overflow: hidden;
    }
    .btn-dialog-register{
        z-index: 99;
        position: absolute;
        bottom: 0;
        width: 100%;
        padding: 10px;
        background: #fff;
        left: 0;
        right: 0;
        display: flex;
        justify-content: center;
        -webkit-box-shadow: 1px 0px 10px 0px rgba(0, 0, 0, 0.1);
        box-shadow: 1px 0px 10px 0px rgba(0, 0, 0, 0.1);
        gap: 10px

    }
    .btn-close {
        background-color: orange;
        color: #fff;
    }
    .btn-close:hover {
        background-color: #ffb833;
        color: #fff;
    }
    .btn-save {
        background-color: green;
        color: #fff;
    }
    .btn-save:hover {
        background-color: #009900;
        color: #fff;
    }
    .btn-register {
        background-color: #0d5ac3;
        color: #fff;
    }
    .btn-register:hover {
        background-color: #3365a9;
        color: #fff;
    }
    .ant-tabs-content-holder{
        height: 70vh;
        overflow-y: overlay;
    }
`