import { Button, Form, Input, Modal } from 'antd';
import React from 'react';
import { useState } from 'react';

function ProposedForm({ open, onCancel, staffInfo, proposedRecord, onSend }) {
    const [dataForm, setDataForm] = useState({
        fullName: staffInfo?.fullName || '',
    });

    const onChangeValue = (e) => {
        setDataForm({ ...dataForm, [e.target.name]: e.target.defaultValue });
    };

    return (
        <Modal
            className="information_modal"
            centered
            title="Biểu mẫu"
            open={open}
            onCancel={onCancel}
            width={'70vw'}
            footer={[
                <Button onClick={onCancel} key="1">
                    Hủy
                </Button>,
                <Button type="primary" onClick={onSend} key="2">
                    Trình lãnh đạo
                </Button>,
                <Button key="3" className="btn-save">
                    Lưu
                </Button>,
            ]}
        >
            <div style={{ display: 'flex', flexDirection: 'column', textAlign: 'center' }}>
                <h4 style={{ textTransform: 'uppercase', fontSize: '20px' }}>Cộng HÒA XÃ HỘI CHỦ NGHĨA VIỆT NAM</h4>
                <p>
                    <span style={{ fontSize: '18px', borderBottom: '2px solid #4d4d4d', paddingBottom: '5px' }}>
                        Độc lập - Tự do - Hạnh Phúc
                    </span>
                </p>
            </div>
            <div className="modal_content">
                <h4
                    style={{
                        textTransform: 'uppercase',
                        textAlign: 'center',
                        padding: '30px 0',
                        fontSize: '18px',
                    }}
                >
                    ĐƠN ĐỀ XUẤT Ý KIẾN THAM MƯU
                </h4>
                <p>Kính gửi: Ban Giám đốc công ty OceanTech</p>

                <Form.Item name="name" onChange={onChangeValue} label="Tôi tên là: ">
                    <Input />
                </Form.Item>
                <Form.Item name="location" onChange={onChangeValue} label="Hiện đang công tác tại vị trí: ">
                    <Input />
                </Form.Item>
                <Form.Item
                    name="problem"
                    onChange={onChangeValue}
                    label="Tôi viết đơn này đề nghị  Công ty xem xét và giải quyết vấn đề: "
                >
                    <Input />
                </Form.Item>
                <Form.Item name="date" onChange={onChangeValue} label="Kể từ ngày: ">
                    <Input />
                </Form.Item>
                <div className="form_block">
                    <Form.Item name="reason" onChange={onChangeValue} label="Nội dung như sau: ">
                        <Input />
                    </Form.Item>
                </div>

                <p>
                    Kính mong Công ty / cá nhân có thẩm quyền xem xét đơn đề nghị và giải quyết vấn đề mà tôi đã nêu ở
                    trên.
                </p>
                <p>
                    Tôi xin cam đoan những thông tin trên hoàn toàn đúng sự thật, nếu có gì sai sót tôi xin chịu trách
                    nhiệm trước Công ty và trước pháp luật.
                </p>
                <p>Tôi xin chân thành cảm ơn!</p>
            </div>
            <div className="modal_signature">
                <div className="date-signature" style={{ display: 'flex', alignItems: 'center', marginBottom: '15px' }}>
                    Hà nội,
                    <Form.Item name="day" onChange={onChangeValue}>
                        <Input className="day" />
                    </Form.Item>
                    ,
                    <Form.Item name="date" onChange={onChangeValue}>
                        <Input />
                    </Form.Item>
                    tháng
                    <Form.Item name="month" onChange={onChangeValue}>
                        <Input />
                    </Form.Item>
                    ,
                    <Form.Item name="year" onChange={onChangeValue}>
                        <Input className="year" />
                    </Form.Item>
                </div>
                <p>Người làm đơn</p>
                <p>{staffInfo.fullName.split(' ').slice(-1).join(' ')}</p>
                <p>{staffInfo.fullName}</p>
            </div>
        </Modal>
    );
}

export default ProposedForm;
